<template>
    <tg-list v-show="IsGroupView" :IsShowAction="false" ModuleName="Ticketing" SubModuleCode="Ticket_Group"
        IdentityColumn="ticketGroupId" :HeaderText="$t('Group')" :LegendArray="[]" :RenderRowActionMethod="() => { }"
        :widgets="[]" :listType="['List']" :ListData="GroupListing" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
        :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData"
        :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
        v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
        v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
        <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'statusId'">
                <listing-status :isfor="'Group'" v-if="isCRMShowStausDrop(data.row.statusId, 'Ticketing')"
                    ref="updatestatus" v-bind:dataid="data.row.ticketGroupId" data-controller="Crm"
                    v-bind:statuslist="StatusListForListing" v-bind:item="data.statusId" data-action="index"
                    v-bind:dataselected="data.row.statusId == 1001 ? 'Active' : 'Inactive'" v-bind:callbackfunction="FetchData"
                    v-bind:isselectedvaluestring="true">
                </listing-status>
                <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.statusId, 'Ticketing')">
                    <span v-bind:class="getStatusClass(data.row.statusId, 'status_id')">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                </a>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'ticketGroupName'">
                <RouterLink :to="'/Ticketing/AddGroup/' + data.row.ticketGroupId" :title="data.row[data.column.COLUMN_NAME]"
                    data-toggle-tooltip="tooltip" data-original-title="Click here to edit Ticketing Group">
                    {{ data.row[data.column.COLUMN_NAME] }}
                </RouterLink>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'groupType'">
                <span><span :title="data.row[data.column.COLUMN_NAME]">{{ data.row[data.column.COLUMN_NAME]=='Escalate'?'Routing': data.row[data.column.COLUMN_NAME]}}</span></span>
            </template>
        </template>
    </tg-list>
</template>
<td class="td4">
                                           
                                        </td>
<script>
import Schema from "@/views/Group/GroupSchema";
import DataServices from "@/services/DataService";
export default {

    data() {
        return {
            GroupListing: [],
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            Headers: [{
                "COLUMN_NAME": "ticketGroupName",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Group Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "groupType",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Group Type",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "users",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Users",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "defaultUser",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Default User",
                "DISPLAY_ORDER": 4
            },
            {
                "COLUMN_NAME": "statusId",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 5,
                
            }

            ],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            IsGroupView: true,
            noRecord: false,
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
            search: '',
            groupType: ''

        }
    },
    created: async function () {
        await this.FetchData();
    },

    methods: {
        paramsToObject(entries) {
            const result = {}
            for (const [key, value] of entries) { // each 'entry' is a [key, value] tupple
                result[key] = value;
            }
            return result;
        },
        leftCommonSearch: function (value) {
            debugger
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        FetchData: async function () {
            // debugger
            var vueObj = this;
            var url = `pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&${vueObj.searchCondition}`;

            await DataServices.GetTicketGroupList(url).then(function (response) {
                //  debugger
                if (response.data != "" && response.data != null) {
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "statusId") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "ticketGroupName") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "groupType") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    if (response.data.length > 0) {
                        vueObj.GroupListing = response.data;
                        vueObj.listheaderbuttons[1].isdisabled = true;
                        vueObj.listheaderbuttons[2].isdisabled = true;
                        vueObj.listheaderbuttons[3].isdisabled = true;
                        vueObj.TotalRecords = response.data[0].totalRecords;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                        vueObj.IsLoad = true;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.GroupListing = [];
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.TotalRecords = 0
                    vueObj.GroupListing = [];
                }
                setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
            })
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "openaddTicketGroup":
                    this.openaddTicketGroup();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "UpdateStatus":
                    this.UpdateStatus(event.id);
                    break;
            }
        },
        openaddTicketGroup: function () {
            var vm = this;
            vm.$router.push({ path: "/Ticketing/AddGroup/0" });
        },
        sortdata: function (sortBy) {

            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },
        getStatusClass: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "Ticketing",
                        refCode: "TALYGEN_TICKET_GROUPS"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        var box = "";
                        if (response.data[0].Status == "Success") {
                            vm.FetchData();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Group')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Group')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);

                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        UpdateStatus: async function (status) {
            var vm = this;
            if (status == 'Active') {
                status = 1001
            }
            else {
                status = 1002
            }
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("Confirm"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                    var VarObject = {
                        ID: val,
                        StatusId: status,
                        IsFor: 'Group'
                    }
                    var objectPostString = JSON.stringify(VarObject);
                    DataServices.CommonUpdateStatus(objectPostString).then(response => {
                        if (response.data.status == "Success") {
                            vm.ShowAlert("Status has been succesfully updated.","success");
                            vm.FetchData();
                        }
                    })
                })
            }
        }
    }


}

</script>
<style>
.icon-solid{
    right:-5px
}
</style>